<template>
  <div>

        <div v-for="item in FoodList" class="food">
          <div v-for="subitem in item.sub_cat">
            <table>
              <tr>
                <td>{{subitem.name}}</td>
                <td v-for="ssubitem in subitem.sub_cat">
                {{ssubitem.name}}
                </td>

              </tr>
            </table>

          </div>
        </div>




  </div>
</template>

<script>
    export default {
        name: "food",
        data(){
          return{
            FoodList:""
          }
        },
        mounted(){
          this.GetFood()
        },
        methods:{
          GetFood(){
            var that = this
            this.$axios.request({
              url:this.$store.state.apiList.getfood,
              mehtod:"GET"
            }).then(function(ret){
              that.FoodList = ret.data.results.slice(3,4)
            })
          }
        }
    }
</script>

<style scoped>
        .food{margin-top: 100px;}
      td{ width:250px; height:40px; line-height:40px; margin:0 auto; margin-bottom: 100px;}
</style>



