<template>
  <div>

        <div v-for="item in ClothList" class="cloth">
          <div v-for="subitem in item.sub_cat">
            <table>
              <tr>
                <td>{{subitem.name}}</td>
                <td v-for="ssubitem in subitem.sub_cat">
                {{ssubitem.name}}
                </td>

              </tr>
            </table>

          </div>
        </div>




  </div>
</template>

<script>
    export default {
        name: "Cloth",
        data(){
          return{
            ClothList:""
          }
        },
        mounted(){
          this.GetCloth()
        },
        methods:{
          GetCloth(){
            var that = this
            this.$axios.request({
              url:this.$store.state.apiList.getcloth,
              mehtod:"GET"
            }).then(function(ret){
              that.ClothList = ret.data.results.slice(2,3)
            })
          }
        }
    }
</script>

<style scoped>
        .cloth{margin-top: 100px;}
      td{ width:250px; height:40px; line-height:40px; margin:0 auto; margin-bottom: 100px;}
</style>



