<template>
    <div @click="WeiBoLogin">
      <img src="../../static/weibo.png" alt="">
    </div>
</template>

<script>
    export default {
        name: "Test",
        data(){
          return {

          }
        },
      methods:{
          WeiBoLogin(){
            this.$axios.request({
              url:this.$store.state.apiList.weibologin,
              method:"GET"
            }).then(function(ret){
              console.log(ret.data.data)
              location.href = ret.data.data
            })
          }
      }
    }
</script>

<style scoped>

</style>



