<template>
        <div class="register-wrap">
            <h3>注册</h3>
            <p v-show="showTishi">{{tishi}}</p>
            <input type="text" placeholder="请输入用户名" v-model="newUsername">
            <input type="password" placeholder="请输入密码" v-model="newPassword">
            <input type="password" placeholder="请再次输入密码" v-model="re_newPassword">

            <input type="text" placeholder="请输入手机号" v-model="phone">
            <input type="text" placeholder="验证码" v-model="code">
            <button style="background-color:grey; color: black" @click="GetCode">获取验证码</button>
            <p>
                <input type="radio" style="width:30px; height:15px;" class="gender" value="female" v-model="gender ">女
                <input type="radio" style="width:30px; height:15px" class="gender" value="male" v-model="gender">男
            </p>
            <button v-on:click="Register">注册</button>
            <span v-on:click="ToLogin">已有账号？马上登录</span>
        </div>
</template>

<script>
    export default {
        name: "Register",
        data(){
          return {
                showTishi: false,
                tishi: '',
                newUsername: '',
                newPassword: '',
                re_newPassword: '',
                gender: '',
                phone: '',
                code:''
          }
        },

      methods:{
          GetCode(){
            var that = this
            if(this.phone === ""){
              alert("请输入手机号")
            }else{
              this.$axios.request({
                url:this.$store.state.apiList.getcode,
                method:'POST',
                data:{
                  phone:this.phone
                },
              }).then(function(ret){
                if(ret.data.data.statusCode === '000000'){
                  that.code = '88888'

                }

              })
            }
          },
          Register(){
            var that = this
            var user = this.newUsername
            var pwd = this.newPassword
            var re_pwd = this.re_newPassword
             if(this.newUsername === "" || this.newPassword === "" ||this.re_newPassword === ""){
                alert("请输入用户名或密码")
            }else{
            if(pwd !== re_pwd){
                this.tishi = "两次输入密码不一致"
                this.showTishi = true
            }else {
              if (this.code !== '88888') {
                alert("验证码错误")
              } else {

              this.$axios.request({
                url: this.$store.state.apiList.register,
                method: "POST",
                data: {
                  user: user,
                  pwd: pwd,
                  phone:this.phone,
                  gender:this.gender
                }
              }).then(function (ret) {
                if (ret.data.code === 1000) {
                  that.tishi = "注册成功，请登陆"
                  that.showTishi = true
                  setTimeout(function () {
                    that.$router.push({path: "/login"})
                  }, 2000)

                } else {
                  console.log(ret)
                  that.tishi = ret.data.error
                  that.showTishi = true


                }
              }).catch(function (ret) {
                alert("发生错误")
              })
            }
            }}
          },
          ToLogin(){
            this.$router.push({path:"/login"})
          }
      }
    }
</script>

<style scoped>
    .gender{display:inline}
    .register-wrap{text-align:center;}
    input{display:block; width:250px; height:40px; line-height:40px; margin:0 auto; margin-bottom: 10px; outline:none; border:1px solid #888; padding:10px; box-sizing:border-box;}
    p{color:red;}
    button{display:block; width:250px; height:40px; line-height: 40px; margin:0 auto; border:none; background-color:#41b883; color:#fff; font-size:16px; margin-bottom:5px;}
    span{cursor:pointer;}
    span:hover{color:#41b883;}
</style>



