<template>
  <div>
      <table>

        <tr>

          <th>书名</th>
          <th>图片</th>
          <th>价格</th>
        </tr>
        <tr v-for="item in FreeCourseList">
          <td>
            <input type="checkbox" @click="HandleCheckClick(item)" v-bind:checked="item.select"/>
            {{item.name}}
          </td>
          <td><img :src= "'../../static/'+item.img"></td>
          <td>{{item.price}}</td>
        </tr>
      </table>
    <div>
      <router-link to="/shoppingcar"><input type="button" value="加入到购物车"></router-link>
    </div>
    <div>
    <div style="text-align:left;">评论</div>
    <hr>
    <div v-for="(item,index) in CommentsList" style="text-align:left;">
      <div>{{index}}回复 </div>
      <div>{{item.content}}</div>
      <div>{{item.time}} |{{item.name}}</div>
      <hr>
    </div>
    <hr>
    <div>
      <div style="text-align:left;">发表评论</div>
      <div style="text-align:left;">
        昵称:<input type="text" v-model="name">
      </div>

      <div style="text-align:left;">
        <div>评论内容:</div>
        <textarea name="" id="" cols="30" rows="10" v-model="textarea"></textarea>
      </div>

      <div class="btn-con" style="text-align:left;">
        <input @click="PostComments" type="button" class="btn" value="提交评论"/>
      </div>
    </div>
    <hr>
    <div>注册用户登录后才能查看发表评论，请 登录 或 注册，访问网站首页。</div>

  </div>

  </div>
</template>

<script>

    export default {
      name: "freecourse",
      data() {
        return {
          FreeCourseList: "",
          checkall: true,
          name:"",
          textarea:"",
          CommentsList:""

        }
      },
      mounted() {
        this.GetFreeCourseList()
        if(this.$store.state.token){
             this.GetComments()
          }
      },
      methods: {

        GetFreeCourseList() {
          var that = this
          this.$axios.request({
            url: this.$store.state.apiList.freecourse,
            method: "GET",
          }).then(function (ret) {
            if (ret.data.code === 1000) {
              // console.log(ret.data)
              that.FreeCourseList = ret.data.data
            } else {
              alert("获取课程失败")
            }

          }).catch(function () {
            alert("发生错误")
          })
        },

        HandleCheckClick(item) {

          item.select = !item.select
          if (item.select) {
            this.$store.state.add_to_car_list.push(item.name)
          } else {
            this.$store.state.add_to_car_list.pop(item.name)
          }
        },
        PostComments(){
            var that = this
            if(this.name === "" || this.textarea === ""){
              alert("昵称或评论为空")
            }else{
              if(that.$store.state.token){
                this.$axios.request({
              url:this.$store.state.apiList.postcomments,
              method:"POST",
                 params:{
                token:this.$store.state.token
              },
              data:{
                name:this.name,
                textarea:this.textarea
              }
            }).then(function(ret){
              // console.log(ret.data)
                 that.CommentsList = ret.data.data
                 that.name = ""
                 that.textarea = ""
            })}else{
                  this.$router.push({name:"Login"})
                }

            }

          },
        GetComments(){
            var that = this
            this.$axios.request({
              url:this.$store.state.apiList.getcomments,
              method:"GET",
              params:{
                token:this.$store.state.token
              },
            }).then(function(ret){
              that.CommentsList = ret.data.data
            })
          },

      },




    }
</script>

<style scoped>

</style>



